



















.goods-mian{
	height: calc(100vH - 90px);
	overflow: hidden;
}
.goods-item{
	position: relative;
	padding: 10px;
}
.goods-item:not(:last-child):after{
	content: '';
	position: absolute;
	left: 10px;
	right: 10px;
	bottom: 0;
	height: 1px;
	background: #ccc;
	border-radius: 10px;
}
.goods-content{
	width: 120px;
	height: 120px;
	margin-right: 10px;
}
.goods-info{
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}
.goods-view{
	position: relative;
	display: block;
	max-height: 100%;
	max-width: 100%;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%,-50%);
	        transform: translate(-50%,-50%);
}
.goods-info-title{
	margin-bottom: 10px;
	font: 18px/1 "\4EFF\5B8B";
	font-weight: bold;
}
.goods-description{
	color: #666;
	font: 13px/1.2 "\4EFF\5B8B";
}
.new-price{
	color: #000;
	margin-bottom: 2px;
	font: 12px/1 "\4EFF\5B8B";
}
.price-box{
	color: #ff0000;
}
.price-info{
	font: 18px/1 "\4EFF\5B8B";
	font-weight: bold;
}
.old-price{
	font: 12px/1 "\4EFF\5B8B";
	color: #999;
}
.goods-cart{
	width: 80px;
	overflow: hidden;
}
.goods-btn{
	display: block;
	width: 25px;
	height: 25px;
	text-align: center;
	line-height: 24px;
	border-radius: 50px;
	cursor: pointer;
	border: 1px solid #2bbbd4;
	color: #fff;
	background: #2bbbd4;
}
.goods-input{
	margin: 0
}
.goods-input-view{
	width: 100%;
	height: 25px;
	text-align: center;
	border: none;
	color: #ff0000;
	font: 16px/1 "\4EFF\5B8B";
	font-weight: bold;
}
.goods-footer{
	position: relative;
	height: 45px;
	-webkit-box-shadow: 0 0 2px rgba(0,0,0,.5);
	        box-shadow: 0 0 2px rgba(0,0,0,.5);
	z-index: 10;
	background: #333;
	color: #fff;
	padding-left: 10px;
}
.goods-tatol-price{
	position: relative;
	padding-right: 10px;
	height: 45px;
	margin-right: 10px;
	font: 14px/45px "\4EFF\5B8B";
}
.goods-tatol-price:after{
	content: '';
	position: absolute;
	right: 0;
	top: 10px;
	bottom: 10px;
	background: #999;
	width: 2px;
	border-radius: 10px;
}
.goods-footer .price-info{
	font-size: 22px;
}
.goods-delivery-price{
	color: #666;
}
.goods-submit{
	height: 45px;
	min-width: 80px;
	border: none;
	padding: 0 10px;
	font: 16px/1 "\4EFF\5B8B";
	background: #999;
	color: #fff;
}
.goods-submit-active{
	background: #008000;
	color: #fff;
}
.goods-disable{
	background: #DDD;
	border-color: #ddd;
	cursor: not-allowed;
}

.goods-box{
	position: relative;
	width: 100%;
	height: 100%;
	overflow: hidden;
}

.app-header{
	position: relative;
	width: 100%;
	height: 45px;
	line-height: 45px;
	padding: 0 10px;
	-webkit-box-shadow: 2px 0 2px rgba(0,0,0,.5);
	        box-shadow: 2px 0 2px rgba(0,0,0,.5);
	z-index: 10;
}
.goback{
	position: absolute;
	width: 45px;
	height: 100%;
	left: 0;
	top: 0;
	text-align: center;
	font: 22px/45px '\4EFF\5B8B';
	font-weight: bold;
	cursor: pointer;
	color: #ff0000;
}
.app-header-title{
	width: 100%;
	font: 18px/45px '\4EFF\5B8B';
	text-align: center;
	font-weight: bold;
}
/*去除浏览器自带样式;*/
*{-webkit-box-sizing: border-box;box-sizing: border-box;margin: 0;padding: 0;}
body{font: 14px/1 "\5FAE\8F6F\96C5\9ED1";}
:focus{outline:none;}
i{font-style: normal;}
a:hover{text-decoration:none;}
ul,li,ol{list-style: none;}
textarea{overflow:auto;resize:none;}
input,textarea,button{outline: none;}
img{border:none;vertical-align:middle;}
h1,h2,h3,h4,h5,h6,p{font-weight: normal;}
table{border-collapse:collapse;border-spacing:0;}
a{text-decoration: none;cursor: pointer;color: #333;}
article,aside,details,figcaption,figure,footer,header,menu,nav,section{display: block;}
/*公共样式*/
.cui-hidden{display: none;}
.cui-show{display: block;}
.cui-pull-left{float: left !important;}
.cui-pull-right{float: right !important;}
.cui-clearfix{display: block;width: 0;height: 0;border: none;visibility: hidden;overflow: hidden;clear: both;}
.cui-clearfix-after:after{content: '';display: block;width: 0;height: 0;border: none;visibility: hidden;overflow: hidden;clear: both;}
.cui-inline{display: inline;}
.cui-inline-block{display: inline-block;}
.cui-hide{display: none;}
.cui-show{display: block;}
.cui-ellipsis{overflow: hidden;white-space: nowrap;text-overflow: ellipsis;}
.cui-ellipsis-2{overflow: hidden;display: -webkit-box;-webkit-box-orient: vertical;text-overflow: ellipsis;-webkit-line-clamp: 2;}
.cui-ellipsis-3{overflow: hidden;display: -webkit-box;-webkit-box-orient: vertical;text-overflow: ellipsis;-webkit-line-clamp: 3;}
.cui-ellipsis-4{overflow: hidden;display: -webkit-box;-webkit-box-orient: vertical;text-overflow: ellipsis;-webkit-line-clamp: 4;}
.cui-ellipsis-5{overflow: hidden;display: -webkit-box;-webkit-box-orient: vertical;text-overflow: ellipsis;-webkit-line-clamp: 5;}
.cui-lg-radius{border-radius: 100%;}
.cui-md-radius{border-radius:50%;}
.cui-sm-radius{border-radius: 20%;}
.cui-xs-radius{border-radius: 10%;}
.cui-lg-main{position: relative;display: block;min-width: 1280px;margin: 0 auto;}
.cui-md-main{position: relative;display: block;max-width: 1280px;min-width: 980px;margin: 0 auto;}
.cui-sm-main{position: relative;display: block;max-width: 980px;min-width: 750px;margin: 0 auto;}
.cui-xs-main{position: relative;display: block;max-width: 750px;margin: 0 auto;}
.cui-main{position: relative;display: block;width: 100%;}
.cui-flex-item{display: block;-webkit-box-flex: 1;-ms-flex: 1;flex: 1;}
.cui-flex-item-2{display: block;-webkit-box-flex: 2;-ms-flex: 2;flex: 2;}
.cui-flex-item-3{display: block;-webkit-box-flex: 3;-ms-flex: 3;flex: 3;}
.cui-flex{display: -webkit-box;display: -ms-flexbox;display: flex;}
.cui-flex-center{display: -webkit-box;display: -ms-flexbox;display: flex;-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center;}
.cui-flex-middle{display: -webkit-box;display: -ms-flexbox;display: flex;-webkit-box-align: center;-ms-flex-align: center;align-items: center;}
.cui-flex-algin{display: -webkit-box;display: -ms-flexbox;display: flex;-webkit-box-pack: center;-ms-flex-pack: center;justify-content: center;-webkit-box-align: center;-ms-flex-align: center;align-items: center;}
.cui-text-left{text-align: left;}
.cui-text-center{text-align: center;}
.cui-text-right{text-align: right;-webkit-box-orient: horizontal;-webkit-box-direction: reverse;-ms-flex-direction: row-reverse;flex-direction: row-reverse;}
.cui-inline-reset>*{display: inline-block;vertical-align: middle;font-size: 14px;}
.cui-inline-reset{font-size: 0;}